.icon-page-list {
  color: #666;
}
.icon-page-list > li {
  margin: 9px 8px;
  float: left;
  text-align: center;
  position: relative;
  overflow: visible;
  border-radius: 5px;
  padding: 8px 5px 0 5px;
  width: 90px;
  min-height: 90px;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  cursor: pointer;
}
.icon-page-list > li > i {
  font-size: 3em;
  display: block;
  margin-bottom: 10px;
}
.icon-page-list > li > span {
  white-space: nowrap;
  word-wrap: break-word;
  font-size: 12px;
}
.icon-page-list > li:hover {
  background: #607D8B;
  color: #fff;
}
